import React from "react";
// nodejs library to set properties for components
import PropTypes from "prop-types";
// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";

import styles from "assets/jss/material-kit-react/components/infoStyle.js";



const useStyles = makeStyles(styles);

const design = {
  textAlign :"justify"
}

export default function InfoArea(props) {
  const classes = useStyles();
  const { title, description, iconColor, vertical, link } = props;
  const iconWrapper = classNames({
    [classes.iconWrapper]: true,
    [classes[iconColor]]: true,
    [classes.iconWrapperVertical]: vertical
  });
  const iconClasses = classNames({
    [classes.icon]: true,
    [classes.iconVertical]: vertical
  });
  return (

    <div className={classes.infoArea}>
      <div className={iconWrapper}>
       <a href={link}> <props.icon className={iconClasses} /></a>
      </div>
      <div className={classes.descriptionWrapper}>
        <h4 className={classes.title}><a href={link}>{title}</a></h4>
        <p className={classes.description}  style = {design} >{description}</p>
      </div>
    </div>
   
  );
}

InfoArea.defaultProps = {
  iconColor: "gray"
};

InfoArea.propTypes = {
  icon: PropTypes.object.isRequired,
  title: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
  iconColor: PropTypes.oneOf([
    "primary",
    "warning",
    "danger",
    "success",
    "info",
    "rose",
    "gray"
  ]),
  vertical: PropTypes.bool
};
